.carousely-enabled {
  position: relative;
}

.carousely-enabled:focus {
  outline: 0;
}

.carousely-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.carousely-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

.carousely-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.carousely-enabled.is-draggable .carousely-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.carousely-enabled.is-draggable .carousely-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.carousely-button {
  position: absolute;
  background: rgba(255, 255, 255, 0.75);
  border: none;
  color: #333;
}

.carousely-button:hover {
  background: #fff;
  cursor: pointer;
}

.carousely-button:focus {
  outline: 0;
  box-shadow: 0 0 0 5px #19f;
}

.carousely-button:active {
  opacity: .6;
}

.carousely-button:disabled {
  opacity: .3;
  cursor: auto;
  pointer-events: none;
}

.carousely-button-icon {
  fill: currentColor;
}

.carousely-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transform: translateY(-50%);
}

.carousely-prev-next-button.previous {
  left: 10px;
}

.carousely-prev-next-button.next {
  right: 10px;
}

.carousely-rtl .carousely-prev-next-button.previous {
  left: auto;
  right: 10px;
}

.carousely-rtl .carousely-prev-next-button.next {
  right: auto;
  left: 10px;
}

.carousely-prev-next-button .carousely-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

.carousely-page-dots {
  position: absolute;
  width: 100%;
  bottom: -25px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.carousely-rtl .carousely-page-dots {
  direction: rtl;
}

.carousely-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  background: #333;
  border-radius: 50%;
  opacity: .25;
  cursor: pointer;
}

.carousely-page-dots .dot.is-selected {
  opacity: 1;
}

body {
