  -webkit-tap-highlight-color: transparent;
}

.no-outline a:focus, .no-outline button:focus {
  outline: none;
}

.button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 233px;
  min-height: 60px;
  padding: 2px;
  font-family: "Mont Semi Bold", "Arial", sans-serif;
  font-size: 16px;
  color: #FFFFFF;
  border-radius: 100px;
  background: linear-gradient(101.23deg, #C39553 0%, #FF3399 100%);
}

.button::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100px;
  background: linear-gradient(110.23deg, #FF3399 0%, #C39553 100%);
  transition: opacity 0.35s cubic-bezier(0.16, 1, 0.3, 1);
  z-index: 2;
  opacity: 0;
}

.no-touch .button:hover::after {
  opacity: 1;
}

.button_smaller {
  min-width: 109px;
  min-height: 48px;
  font-size: 14px;
}

.button_smaller .button__link {
  padding: 13px 19px;
