  font-family: "Mont Semi Bold", "Arial", sans-serif;
}

.button_bold .button__link {
  font-family: "Mont Bold", "Arial", sans-serif;
}

.button_dark {
  min-width: 81px;
  min-height: 48px;
  padding: 14px 16px;
  font-size: 14px;
  background: #201E29;
  transition: background 0.35s cubic-bezier(0.16, 1, 0.3, 1);
}

.button_dark::after {
  display: none;
}

.no-touch .button_dark:hover {
  background: #292735;
