.page-content {
	max-width: 936px;
	padding: 56px 0 100px;
	margin: 0 auto;

	@include sm {
		padding-top: 40px; }

	&__title {
		padding-bottom: 56px;

		@include sm {
			padding-bottom: 40px; } }

	&__description {
		color: $light-gray;
		font-size: 16px;
		line-height: 1.5;

		@include sm {
			font-size: 14px;
			line-height: 1.5714286; }

		p {
			margin: 24px 0; }

		a {
			color: $golden;

			@include hover {
				color: $white; } }

		b {
			font-weight: 400;
			color: $white; } } }
